"use client";

import { createContext, useContext, useState, type ReactNode } from "react";

interface IntroContextValue {
    introVisible: boolean;
    setIntroVisible: (visible: boolean) => void;
}

const IntroContext = createContext<IntroContextValue | undefined>(undefined);

export function useIntro() {
    const context = useContext(IntroContext);
    if (!context) {
        throw new Error("useIntro must be used within IntroProvider");
    }
    return context;
}

export default function IntroProvider({ children }: { children: ReactNode }) {
    const [introVisible, setIntroVisible] = useState(false);

    return (
        <IntroContext.Provider value={{ introVisible, setIntroVisible }}>
            {children}
        </IntroContext.Provider>
    );
}

